<template lang="pug">
.page-safe-form
    my-nav-bar(title="I品车险")
    van-cell-group
        van-field(label="姓名" placeholder="请输入姓名" v-model="form.name")
        van-field(label="电话" placeholder="请输入电话" v-model="form.phone")
        van-field(label="车牌号" placeholder="请输入车牌号" v-model="form.car_no")
        my-form-uploader(label="行驶证" placeholder="请选择" v-model="form.driver_card")
        my-form-select(label="车辆险种" placeholder="请选择" :actions="optionActions" v-model="form.option")
    my-bottom-bar.bottom(:click="save")
        .g-bottom-btn 提交
</template>
<script>
export default {
    data () {
        return {
            optionActions: [
                {
                    name: '车损险',
                    value: '车损险'
                }, {
                    name: '三者险',
                    value: '三者险'
                }, {
                    name: '盗抢险',
                    value: '盗抢险'
                }, {
                    name: '座位险',
                    value: '座位险'
                }, {
                    name: '自燃险',
                    value: '自燃险'
                }, {
                    name: '涉水险',
                    value: '涉水险'
                }, {
                    name: '玻璃险',
                    value: '玻璃险'
                }, {
                    name: '划痕险',
                    value: '划痕险'
                }, {
                    name: '随车行李险',
                    value: '随车行李险'
                }, {
                    name: '新增设备险',
                    value: '新增设备险'
                }
            ],
            form: {
                name: '',
                phone: '',
                car_no: '',
                driver_card: '',
                option: ''
            }
        };
    },
    methods: {
        async save () {
            try {
                await this.$http.post('/mobile/finance/safeadd', this.form);
                this.$dialog.alert({
                    message: '保存成功'
                });
                history.back();
            } catch (error) {
                console.log(error);
            }
        }
    }
};
</script>



